import { useEffect, useState } from 'react'
import { useLocation } from "react-router-dom";
import debounce from 'lodash/debounce'
import { useDispatch } from 'react-redux';
import ReactSlider from 'react-slider'
import { eventBusService } from '../services/event-bus.service'
import queryString from 'query-string'


export function SearchFilter() {

    const [filters, setFilters] = useState({
        name: null,
        locationFilters: {
          Home: false,
          Office: false,
          Balcony: false,
          Yard: false,
        },
        difficulty: null,
        lightning: null,
        watering: null,
        priceRange: {
          min: 0,
          max: 1000,
        },
      });

  function handleFilterChange(event) {
    const { name, type, value, checked } = event.target;

    if (type === 'checkbox') {
      // Handle checkbox changes (location filters)
      setFilters((prevFilters) => ({
        ...prevFilters,
        locationFilters: {
          ...prevFilters.locationFilters,
          [name]: checked,
        },
      }));
    } else if (type === 'radio') {
      // Handle radio button changes (difficulty, lightning, watering)
      setFilters((prevFilters) => ({
        ...prevFilters,
        [name]: value,
      }));
    }
  }


    const dispatch = useDispatch()
    const location = useLocation()
    useEffect(() => {
        console.log('filters', filters)
        const query = queryString.stringify(filters)
        eventBusService.emit('filter', filters)
    }, [filters, dispatch, location])



      function handleResetFilters() {
        setFilters({
          name: null,
          locationFilters: {
            Home: false,
            Office: false,
            Balcony: false,
            Yard: false,
          },
          difficulty: null,
          lightning: null,
          watering: null,
          priceRange: {
            min: 0,
            max: 1000,
          },
        });
      }
    return (
        <section className='filter-container'>
            <h2>Location</h2>
            <label>
                <input
                    type='checkbox'
                    name='Home'
                    checked={filters.locationFilters.Home}
                    // value={filters.locationFilters.Home !== null ? filters.locationFilters.Home : ''}
                    onChange={handleFilterChange}
                />
                Home
            </label>
            <label>
                <input
                    type='checkbox'
                    name='Office'
                    checked={filters.locationFilters.Office}
                    // value={filters.locationFilters.Office !== null ? filters.locationFilters.Office : ''}
                    onChange={handleFilterChange}
                />
                Office
            </label>
            <label>
                <input type="checkbox"
                    name="Balcony"
                    checked={filters.locationFilters.Balcony}
                    // value={filters.locationFilters.Balcony !== null ? filters.locationFilters.Balcony : ''}
                    onChange={handleFilterChange}
                />
                Balcony
            </label>
            <label>
                <input type="checkbox"
                    name="Yard"
                    checked={filters.locationFilters.Yard}
                    // value={filters.locationFilters.Yard !== null ? filters.locationFilters.Yard : ''}
                    onChange={handleFilterChange}
                />
                Yard
            </label>

            <h2>Difficulty</h2>
            <label>
                <input
                    type="radio"
                    name="difficulty"
                    value="Survivor"
                    checked={filters.difficultyFilter === "Survivor"}
                    onChange={handleFilterChange}
                />
                Survivor
            </label>
            <label>
                <input
                    type="radio"
                    name="difficulty"
                    value="Amateur"
                    checked={filters.difficultyFilter === "Amateur"}
                    onChange={handleFilterChange}
                />
                Amateur
            </label>
            <label>
                <input
                    type="radio"
                    name="difficulty"
                    value="Pro"
                    checked={filters.difficultyFilter === "Pro"}
                    onChange={handleFilterChange}
                />
                Pro
            </label>

            <h2>Lightning</h2>
            <label>
                <input
                    type="radio"
                    name="lightning"
                    value="Low"
                    checked={filters.lightningFilter === "Low"}
                    onChange={handleFilterChange}
                />
                Low
            </label>
            <label>
                <input
                    type="radio"
                    name="lightning"
                    value="Medium"
                    checked={filters.lightningFilter === "Medium"}
                    onChange={handleFilterChange}
                />
                Medium
            </label>
            <label>
                <input
                    type="radio"
                    name="lightning"
                    value="High"
                    checked={filters.lightningFilter === "High"}
                    onChange={handleFilterChange}
                />
                High
            </label>

            <h2>Watering</h2>
            <label>
                <input
                    type="radio"
                    name="watering"
                    value="Low"
                    checked={filters.wateringFilter === "Low"}
                    onChange={handleFilterChange} />
                Low
            </label>
            <label>
                <input type="radio"
                    name="watering"
                    value="Medium"
                    checked={filters.wateringFilter === "Medium"}
                    onChange={handleFilterChange} />
                Medium
            </label>
            <label>
                <input type="radio"
                    name="watering"
                    value="High"
                    checked={filters.wateringFilter === "High"}
                    onChange={handleFilterChange} />
                High
            </label>

            <h2>Price Range</h2>
            <div className="price-container">
                <div className="price-input">
                    <input
                        type="number"
                        min="0"
                        max="1000"
                        value={filters.priceRange.min}
                        onChange={handleFilterChange}
                    />
                    <div className='separator'>-</div>
                    <input
                        type="number"
                        min="0"
                        max="1000"
                        value={filters.priceRange.max}
                        onChange={handleFilterChange}
                    />
                </div>
                <div className='slider-container'>
                    <ReactSlider
                        className="horizontal-slider"
                        thumbClassName="slider-thumb"
                        trackClassName="slider-track"
                        value={[filters.priceRange.min, filters.priceRange.max]}
                        ariaLabelledby={['first-slider-label', 'second-slider-label']}
                        renderTrack={(props, state) => (
                            <div
                                {...props}
                                className={`slider-track ${state.index === 1 ? "track-before" : "track-after"}`}
                            />
                        )}
                        onChange={handleFilterChange}
                        minDistance={50}
                        min={0}
                        max={1000}
                        pearling={true}
                    />
                </div>
            </div>
            <button type="reset" onClick={handleResetFilters}>Reset</button>
        </section>
    )
}
